body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
a {
  color: rgb(124, 55, 173);
}
.main-container {
  min-width: 320px;
  min-height: 100vh;
  background-image: linear-gradient(135deg, #672083, #a246c7, #a569bd);
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 80%, 0 100%);
          clip-path: polygon(0 0, 100% 0, 100% 80%, 0 100%);
}

.inside-container {
  padding-top: 170px;
  -webkit-animation-name: intro-ani;
          animation-name: intro-ani;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation-duration: 1000ms;
          animation-duration: 1000ms;
  -webkit-animation-delay: 500ms;
          animation-delay: 500ms;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  opacity: 1;
}
.social-icon a {
  color: white !important;
}

@-webkit-keyframes intro-ani {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(-30px, 0px, 0px);
            transform: translate3d(-30px, 0px, 0px);
  }
  100% {
    opacity: 1;
    -webkit-transform: none;
            transform: none;
  }
}

@keyframes intro-ani {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(-30px, 0px, 0px);
            transform: translate3d(-30px, 0px, 0px);
  }
  100% {
    opacity: 1;
    -webkit-transform: none;
            transform: none;
  }
}

.main-heading,
.intro {
  font-size: 70px;
  color: white;
  font-weight: bold;
  font-family: cabin, sans-serif;
  text-align: center;
}

.intro-font {
  font-size: 20px;
  margin-left: auto;
}

.social-icon {
  text-align: center;
  font-size: 40px;
  color: white;
}

.social-icon:visited,
.social-icon:active {
  color: white;
}

.icon {
  margin: 7px;
}

.icon:hover {
  opacity: 0.5;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .main-container {
    -webkit-clip-path: none;
            clip-path: none;
  }
  .inside-container {
    padding-top: 180px;
  }
  .main-heading,
  .intro {
    font-size: 40px;
    margin: auto;
    color: white;
    font-weight: bold;
    font-family: cabin, sans-serif;
    text-align: center;
    margin: 30px auto;
  }

  .intro-font {
    font-size: 20px;
    font-weight: normal;
  }
  .social-icon {
    font-size: 28px;
  }
}

.about-container {
  margin: 0px;
  padding: 50px 0px 50px 50px;
}

.sub-title {
  font-family: cabin, sans-serif;
  font-size: 24px;
  font-weight: bold;
  color: rgb(218, 17, 84);
}

.about-border {
  border-bottom: 3px solid rgb(174, 102, 223);
}

.about-contents {
  font-family: cabin, sans-serif;
  font-size: 19px;
  width: 100%;
  line-height: 32px;
  padding: 0px 33px;
}

.about-inside {
  display: flex;
  justify-content: space-between;
}

.image-content {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.img-picture {
  width: 332px;
  height: 300px;
  border-radius: 50%;
  box-shadow: 15px 15px 0px 0px rgb(174, 102, 223);
}

.img-picture:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .about-container {
    margin: 0px 0px;
    padding: 1px 10px;
  }
  .about-contents {
    width: 100%;
    margin-top: 5px;
  }
  .about-inside {
    display: block;
  }
  .image-content {
    display: block;
    width: 100%;
    text-align: center;
  }
  .img-picture {
    margin: 10px auto;
    scale: 0.8;
  }
  .sub-title {
    padding-top: 10px;
  }
}

