  justify-content: space-between;
}

.git-container {
  height: inherit;
  width: 30%;
  box-shadow: 2px 2px 10px 0px black;
  display: flex;
  border-radius: 5px;
}

.containOne {
  width: 50%;
}

.containTwo {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.project-title {
  text-align: left;
  width: 50%;
  border-bottom: 5px solid rgb(124, 55, 173);
