}

.project-icon {
  padding: 0px 2px;
  color: rgb(124, 55, 173);
}

.project-web {
  background: rgb(240, 230, 246);
  padding: 3px 25px 3px 3px;
  -webkit-clip-path: polygon(0px 0%, 100% 0%, calc(100% - 20px) 100%, 0% 100%);
          clip-path: polygon(0px 0%, 100% 0%, calc(100% - 20px) 100%, 0% 100%);
}

.project-image {
  margin: 3px auto;
  display: flex;
  justify-content: center;
}

.project-text {
  display: flex;
  justify-content: space-between;
}

.project-tech {
  background: rgb(124, 55, 173);
