@import url("https://fonts.googleapis.com/css?family=Cabin&display=swap");
.contact-container {
  margin: 0px;
  padding: 50px 0px 50px 50px;
  background-image: linear-gradient(135deg, white, white, rgb(190, 129, 231));
}

.sub-title {
  font-family: cabin, sans-serif;
  font-size: 24px;
  font-weight: bold;
  color: rgb(218, 17, 84);
}

.about-border {
  border-bottom: 3px solid rgb(174, 102, 223);
}

.form-container {
  width: 100%;
  display: flex;
  justify-content: center;
}

.form-inside {
  width: 50%;
}

.btn-primary {
  background-color: rgb(174, 102, 223) !important;
  border: none !important;
  border-bottom: 7px solid rgb(130, 50, 191) !important;
}

.btn-primary:hover {
  transform: scale(1.1);
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .contact-container {
    margin: 20px 0px;
    padding: 1px 10px;
  }
  .form-container {
    display: block;
    margin-top: 20px;
  }
  .form-inside {
    width: 100%;
  }
}
